@import url(https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* GENERAL */

* {
  list-style: none;
  font-family: "Josefin Sans", sans-serif;
}

.App {
  height: 100%;
  width: 100%;
  background-image: -webkit-linear-gradient(
    30deg,
    rgb(8, 29, 39) 50%,
    rgb(11, 33, 48) 50%
  );
  text-align: center;
}

/* NAVIGATION */
nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  min-height: 10vh;
  background: rgb(12, 41, 56);
  color: white;
}

.dog_logo {
  display: -webkit-flex;
  display: flex;
  object-fit: contain;
  width: 50%;
  max-height: 70px;
  padding: 10px;
}

.nav-links {
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  text-transform: uppercase;
  margin-inline: 10px;
  font-size: 25px;
  font-weight: bold;
  list-style: none;
}

.nav_style {
  color: white;
  text-decoration: none;
  margin-right: 20px;
  padding: 10px;
}

/* HOME COMPONENT */

.home {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  height: 100vh;
  min-height: 650px;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  background-image: -webkit-linear-gradient(
    30deg,
    rgb(8, 29, 39) 50%,
    rgb(11, 33, 48) 50%
  );
}

.home > div {
  -webkit-flex: 1 1 150px;
          flex: 1 1 150px;
  height: 500px;
}

.home_images {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  padding-right: 50px;
  width: 100%;
}

.home_image {
  object-fit: cover;
  display: -webkit-flex;
  display: flex;
  width: 50%;
  height: 70%;
}

.home_info {
  color: white;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 100px;
}

.home_title {
  font-size: 55px;
  margin: 0;
  padding: 0;
}

/* DOGS COMPONENT */

.dogs {
  height: 100vh;
  min-height: 600px;
  background-image: -webkit-linear-gradient(
    30deg,
    rgb(8, 29, 39) 50%,
    rgb(11, 33, 48) 50%
  );
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 50px;
}

/* DOGS COMPONENT / DOGS ROW */

.row h2 {
  display: -webkit-flex;
  display: flex;
  margin-left: 10px;
  margin-bottom: 10px;
  font-size: 35px;
  font-weight: bold;
  color: white;
}

.row_title {
  width: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.row_dogs {
  display: -webkit-flex;
  display: flex;
  min-height: 200px;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
  padding-top: 40px;
  padding-bottom: 50px;
  background: rgb(12, 41, 56);
}

.row_dogs::-webkit-scrollbar {
  display: none;
}

.arrow__btn {
  position: absolute;
  color: rgb(255, 255, 255, 0.5);
  text-decoration: none;
  font-size: 6em;
  background: rgb(0, 0, 0);
  width: 80px;
  height: 348px;
  padding: 20px;
  margin-top: 218px;
  border: none;
  text-align: center;
  z-index: 1;
}

.left-arrow {
  top: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.35);
}

.right-arrow {
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.35);
}

.arrow__btn:hover {
  color: white;
}

.arrow__btn:focus {
  outline: none !important;
  outline-offset: none !important;
}

.row_dog {
  object-fit: cover;
  width: 200px;
  height: 250px;
  margin-right: 10px;
  border: 1px solid rgb(8, 29, 39);
  border-radius: 1px;
  transition: -webkit-transform 450ms;
  transition: transform 450ms;
  transition: transform 450ms, -webkit-transform 450ms;
}

.row_dog:hover {
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}

/* DOG DETAIL COMPONENT */

.dog_detail {
  height: 100vh;
  min-height: 600px;
  display: -webkit-flex;
  display: flex;
  padding-top: 40px;
  -webkit-justify-content: center;
          justify-content: center;
  background-image: -webkit-linear-gradient(
    30deg,
    rgb(8, 29, 39) 50%,
    rgb(11, 33, 48) 50%
  );
}

/* CARD CONTAINER */

.wrapper {
  position: absolute;
  margin-top: 80px;
  width: 750px;
  display: -webkit-flex;
  display: flex;
  box-shadow: 0 1px 20px 0 rgba(69, 90, 100, 0.08);
}

.wrapper .left {
  width: 35%;
  background: linear-gradient(to right, rgb(243, 147, 61), rgb(204, 95, 0));
  padding: 30px 25px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  text-align: center;
  color: #fff;
}

.wrapper .left img {
  border-radius: 5px;
  margin-bottom: 10px;
}

.wrapper .left h4 {
  margin-bottom: 10px;
}

.wrapper .left p {
  font-size: 12px;
}

.wrapper .right {
  width: 65%;
  background: #fff;
  padding: 30px 25px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.wrapper .right .info,
.wrapper .right .owner {
  margin-bottom: 25px;
}

.wrapper .right .info h3,
.wrapper .right .owner h3 {
  margin-bottom: 15px;
  padding-bottom: 5px;
  border-bottom: 1px solid #e0e0e0;
  color: #353c4e;
  text-transform: uppercase;
  letter-spacing: 5px;
}

.wrapper .right .info_data,
.wrapper .right .owner_data {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.wrapper .right .info_data .data,
.wrapper .right .owner_data .data {
  width: 45%;
}

.wrapper .right .info_data .data h4,
.wrapper .right .owner_data .data h4 {
  color: #353c4e;
  margin-bottom: 5px;
}

.wrapper .right .info_data .data p,
.wrapper .right .owner_data .data p {
  font-size: 13px;
  margin-bottom: 10px;
  color: #919aa3;
}

#dog_detail_image {
  object-fit: cover;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  vertical-align: middle;
  min-width: 200px;
  min-height: 200px;
  max-width: 200px;
  max-height: 200px;
  border-radius: 50%;
  border: 2px solid white;
}

#breed {
  text-transform: capitalize;
}

/* MOBILE SCREEN */
@media only screen and (max-width: 768px) {
  .logo {
    padding-left: 15px;
  }
  .dog_logo {
    object-fit: cover;
    height: 21px;
    width: 100%;
  }
  .nav-links {
    font-size: 20px;
  }
  .home_images {
    display: none;
  }
  .home_info {
    padding-top: 0px;
    font-size: 15px;
  }
  .home_title {
    font-size: 45px;
  }
  .row {
    padding-top: 20px;
  }
  .row_dogs {
    min-height: 160px;
    padding: 20px;
    padding-top: 20px;
  }
  .row h2 {
    font-size: 20px;
  }
  .row_dog {
    width: 100px;
    height: 150px;
  }
  .arrow__btn {
    display: none;
  }
  .dog_detail {
    width: 100%;
    height: 100%;
    padding-top: 0px;
    margin-top: 0px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .wrapper {
    position: relative;
    width: 70%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 0px;
    margin-top: -100px;
  }
  .wrapper .left {
    width: 100%;
    max-height: 160px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 5px;
    color: #fff;
  }
  .wrapper .right {
    width: 100%;
    max-height: 200px;
    background: #fff;
    padding: 30px 25px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  #dog_detail_image {
    min-width: 100px;
    min-height: 100px;
    max-width: 100px;
    max-height: 100px;
  }
  .wrapper .right .info,
  .wrapper .right .owner {
    margin-bottom: 0px;
  }

  .wrapper .right .info h3,
  .wrapper .right .owner h3 {
    margin-bottom: 0px;
    margin-top: -10px;
    padding-bottom: 0px;
    border-bottom: 1px solid #e0e0e0;
    color: #353c4e;
    text-transform: uppercase;
    letter-spacing: 5px;
  }

  .wrapper .right .info_data,
  .wrapper .right .owner_data {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }

  .wrapper .right .info_data .data,
  .wrapper .right .owner_data .data {
    width: 45%;
  }

  .wrapper .right .info_data .data h4,
  .wrapper .right .owner_data .data h4 {
    color: #353c4e;
    margin-bottom: 0px;
  }

  .wrapper .right .info_data .data p,
  .wrapper .right .owner_data .data p {
    font-size: 13px;
    margin-bottom: 30px;
    color: #919aa3;
  }
}

